body {
  background: rgb(65, 65, 255);
  width: 100%;
  min-height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 1rem;
  margin: 0;
}

.container {
  background: white;
  border: 1px solid #ccc;
  border-radius: 20px;
  width: 100%;
  max-width: 622px;
  padding: 15px;
  box-shadow: 0 2px 6px rgba(0, 0, 0, 0.2);
  transition: box-shadow 0.2s ease, transform 0.2s ease;
}

.header {
  border-bottom: solid thin;
  padding-bottom: 0.5rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-wrap: wrap; /* allows timer to drop below heading */
  gap: 0.5rem;
}

#mainHeading {
  font-size: clamp(1.2rem, 4vw, 1.6rem);
  margin: 0;
  flex: 1;
}

#setTiming {
  display: flex;
  align-items: center;
  background: lightblue;
  border-radius: 8px;
  padding: 3px 8px;
  font-size: 0.9rem;
  flex-shrink: 0;
}

#timeText {
  margin-left: 5px;
}

#timing {
  background: black;
  color: white;
  border-radius: 6px;
  padding: 2px 6px;
  min-width: 24px;
  text-align: center;
}

/* Question text */
.main {
  margin-top: 1rem;
  font-size: clamp(1rem, 2.5vw, 1.1rem);
}

#options {
  list-style-type: none;
  padding: 0;
  margin-top: 1rem;
}

#options li {
  margin: 0.5rem 0;
  font-size: clamp(0.9rem, 2.5vw, 1rem);
  line-height: 1.4;
}

/* Next button */
#nextQuestion {
  background: lightblue;
  padding: 10px 15px;
  border-radius: 8px;
  cursor: pointer;
  font-size: 1rem;
  display: inline-block;
  text-align: center;
  margin-top: 1rem;
}

.footer {
  margin-top: 1.5rem;
  text-align: center;
}

/* Tablet */
@media (max-width: 768px) {
  .container {
    padding: 12px;
  }

  #nextQuestion {
    width: 100%;
  }
}

/* Phone */
@media (max-width: 480px) {
  .container {
    padding: 10px;
    border-radius: 12px;
  }

  #mainHeading {
    text-align: center;
    width: 100%;
  }

  #setTiming {
    justify-content: center;
    width: 100%;
  }

  #nextQuestion {
    width: 100%;
    font-size: 1rem;
  }
}